/**************************************************************************
 * MIXINS > WIDGET
 * @docs https://vuejs.org/v2/guide/mixins.html
 ***************************************************************************/

export default {
  props: {
    shop: {
      type: String,
      default: null
    }
  },

  data: () => ({
    // --> STATE <--

    error: false,

    errorMessage: "Sorry but something went wrong"
  }),

  computed: {
    widgetId() {
      return this.$options.name
        .trim()
        .split(/(?=[A-Z])/)
        .join("-")
        .toLowerCase()
    }
  }
}
