/**************************************************************************
 * IMPORTS
 ***************************************************************************/

// NPM
import throttle from "lodash/throttle"

/**************************************************************************
 * MIXINS > TRIGGER
 * @docs https://vuejs.org/v2/guide/mixins.html
 ***************************************************************************/

export default {
  props: {
    trigger: {
      type: String,
      default: "immediate",
      validator(x) {
        return [
          "click",
          "delay",
          "exit-intent",
          "hover",
          "immediate",
          "position"
        ].includes(x)
      }
    },
    triggerClickSelector: {
      type: String,
      default: null
    },
    triggerDelayNumber: {
      type: Number,
      default: 0
    },
    triggerDelayUnit: {
      type: String,
      default: "second",
      validator(x) {
        return ["second", "minute", "hour"].includes(x)
      }
    },
    triggerHoverSelector: {
      type: String,
      default: null
    },
    triggerScrollPercentage: {
      type: Number,
      default: null
    },
    triggerScrollPixels: {
      type: Number,
      default: null
    }
  },

  data() {
    return {
      // --> STATE <--

      triggered: false,

      delayHandle: null,

      delayUnitsInMs: {
        second: 1000,
        minute: 60 * 1000,
        hour: 60 * 60 * 1000
      }
    }
  },

  created() {
    // Trigger immediately when asked or inside the editor
    if (this.trigger === "immediate" || this.editor) {
      this.triggered = true
    }

    // Trigger after at a certain delay
    if (this.trigger === "delay") {
      const delay =
        this.triggerDelayNumber * this.delayUnitsInMs[this.triggerDelayUnit]

      this.delayHandle = setInterval(() => {
        this.triggered = true
      }, delay)
    }

    // Trigger at a specific page position
    if (this.trigger === "position") {
      window.addEventListener("scroll", this.onWindowScroll)
    }

    // Trigger on exit intent
    if (this.trigger === "exit-intent") {
      document.addEventListener("mouseleave", this.onDocumentMouseLeave)
    }

    // Trigger on click or hover
    if (["click", "hover"].includes(this.trigger)) {
      const elements = document.querySelectorAll(this.triggerClickSelector)
      const eventName = this.trigger === "hover" ? "mouseover" : this.trigger

      elements.forEach(element => {
        element.addEventListener(eventName, () => {
          this.triggered = true
        })
      })
    }
  },

  destroyed() {
    // Cleaning intervals and event listeners
    if (this.trigger === "delay") {
      clearInterval(this.delayHandle)
    } else if (this.trigger === "exit-intent") {
      window.removeEventListener("mouseleave", this.onDocumentMouseLeave)
    } else if (this.trigger === "position") {
      window.removeEventListener("scroll", this.onWindowScroll)
    }
  },

  methods: {
    // --> HELPERS <--

    getScrollPercentage() {
      const el = document.documentElement || document.body

      return Math.ceil(
        (el.scrollTop / (el.scrollHeight - el.clientHeight)) * 100
      )
    },

    // --> EVENT LISTENERS <--

    onDocumentMouseLeave() {
      // Trigger only when exiting from the top
      if (event.clientY <= 20) {
        this.triggered = true
      }
    },

    onWindowScroll: throttle(function() {
      // Trigger when we reached the position in percentage
      if (this.triggerScrollPercentage !== null) {
        const scrollPercentage = this.getScrollPercentage()

        this.triggered =
          scrollPercentage >= this.triggerScrollPercentage ||
          this.triggerScrollPercentage === 0
      }

      // Trigger when we reached the position in pixels
      if (this.triggerScrollPixels !== null) {
        this.triggered =
          window.scrollY >= this.triggerScrollPixels ||
          this.triggerScrollPixels === 0
      }
    }, 500)
  }
}
