/**************************************************************************
 * MIXINS > CLOSE
 * @docs https://vuejs.org/v2/guide/mixins.html
 ***************************************************************************/

export default {
  data: () => ({
    // --> STATE <--

    closed: null
  }),

  mounted() {
    if (!this.editor) {
      // Hide widget when it has already been closed
      this.closed = !!localStorage.getItem(`${this.widgetId}-closed`)
    } else {
      this.closed = false
    }
  },

  methods: {
    // --> EVENT LISTENERS <--

    onClose() {
      this.closed = true

      if (!this.editor) {
        localStorage.setItem(`${this.widgetId}-closed`, true)
      }
    }
  }
}
