<!-- *************************************************************************
     TEMPLATE
     ************************************************************************* -->

<template lang="pug">
a(
  :class=`[
    "c-widget-branding",
    "c-widget-branding--" + theme
  ]`
  :href="href"
  target="_blank"
)
  span.c-widget-branding__powered Powered by

  span.c-widget-branding__logo

  span.c-widget-branding__brand Growth Bunker
</template>

<!-- *************************************************************************
     SCRIPT
     ************************************************************************* -->

<script>
// PROJECT: MIXINS
import ThemeMixin from "@/mixins/ThemeMixin"

export default {
  mixins: [ThemeMixin],

  computed: {
    href() {
      let url = "https://growthbunker.com/"

      if (window && window.location && window.location.host) {
        url += `?ref=${window.location.host}`
      }

      return url
    }
  }
}
</script>

<!-- *************************************************************************
     STYLE
     ************************************************************************* -->

<style lang="scss">
// VARIABLES
$c: ".c-widget-branding";

#{$c} {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 8px 10px;
  border-top-width: 1px;
  border-top-width: 1px;
  border-top-style: solid;
  text-decoration: none;
  font-size: 11px;
  transition: color 125ms linear;
  user-select: none;
  cursor: pointer;

  #{$c}__powered {
    margin-right: 5px;
  }

  #{$c}__logo {
    display: inline-block;
    margin-right: 2px;
    width: 18px;
    height: 15px;
    background-size: cover;
  }

  // --> THEMES <--

  @each $theme in $themes {
    $themeName: map-get($theme, "name");

    &--#{$themeName} {
      border-top-color: mdg($theme, "borders", "default", "primary");
      background-color: mdg($theme, "backgrounds", "default", "tertiary");
      color: mdg($theme, "fonts", "default", "primary");

      #{$c}__logo {
        @if ($themeName == "dark") {
          background-image: url(data:image/png;base64,);
        } @else {
          background-image: url(data:image/png;base64,);
        }
      }

      &:hover {
        color: mdg($theme, "fonts", "default", "secondary");
      }
    }
  }
}
</style>
