/**************************************************************************
 * MIXINS > POSITION BANNER
 * @docs https://vuejs.org/v2/guide/mixins.html
 ***************************************************************************/

export default {
  props: {
    position: {
      type: String,
      default: "bottom",
      validator(x) {
        return ["bottom", "top"].includes(x)
      }
    }
  }
}
